@use "sass:color";

// Comment colors may not be WCAG compliant,
// so adjustments made here
$comment-lighter: color.scale(#928374, $lightness: 8%);
$comment-darker: color.scale(#928374, $lightness: -17%);

.highlight, .highlight .w {
color: #282828;
background-color: #fbf1c7;
}
.highlight .err {
color: #9d0006;
background-color: #fbf1c7;
font-weight: bold;
}
.highlight .c, .highlight .ch, .highlight .cd, .highlight .cm, .highlight .cpf, .highlight .c1, .highlight .cs {
color: $comment-darker;
font-style: italic;
}
.highlight .cp {
color: #427b58;
}
.highlight .nt {
color: #9d0006;
}
.highlight .o, .highlight .ow {
color: #282828;
}
.highlight .p, .highlight .pi {
color: #282828;
}
.highlight .gi {
color: #79740e;
background-color: #fbf1c7;
}
.highlight .gd {
color: #9d0006;
background-color: #fbf1c7;
}
.highlight .gh {
color: #79740e;
font-weight: bold;
}
.highlight .ge {
font-style: italic;
}
.highlight .ges {
font-weight: bold;
font-style: italic;
}
.highlight .gs {
font-weight: bold;
}
.highlight .k, .highlight .kn, .highlight .kp, .highlight .kr, .highlight .kv {
color: #9d0006;
}
.highlight .kc {
color: #8f3f71;
}
.highlight .kt {
color: #b57614;
}
.highlight .kd {
color: #af3a03;
}
.highlight .s, .highlight .sb, .highlight .sc, .highlight .dl, .highlight .sd, .highlight .s2, .highlight .sh, .highlight .sx, .highlight .s1 {
color: #79740e;
font-style: italic;
}
.highlight .si {
color: #79740e;
font-style: italic;
}
.highlight .sr {
color: #79740e;
font-style: italic;
}
.highlight .sa {
color: #9d0006;
}
.highlight .se {
color: #af3a03;
}
.highlight .nn {
color: #427b58;
}
.highlight .nc {
color: #427b58;
}
.highlight .no {
color: #8f3f71;
}
.highlight .na {
color: #79740e;
}
.highlight .m, .highlight .mb, .highlight .mf, .highlight .mh, .highlight .mi, .highlight .il, .highlight .mo, .highlight .mx {
color: #8f3f71;
}
.highlight .ss {
color: #076678;
}

@media (prefers-color-scheme: dark) {
  .highlight, .highlight .w {
  color: #fbf1c7;
  background-color: #282828;
  }
  .highlight .err {
  color: #fb4934;
  background-color: #282828;
  font-weight: bold;
  }
  .highlight .c, .highlight .ch, .highlight .cd, .highlight .cm, .highlight .cpf, .highlight .c1, .highlight .cs {
  color: $comment-lighter;
  font-style: italic;
  }
  .highlight .cp {
  color: #8ec07c;
  }
  .highlight .nt {
  color: #fb4934;
  }
  .highlight .o, .highlight .ow {
  color: #fbf1c7;
  }
  .highlight .p, .highlight .pi {
  color: #fbf1c7;
  }
  .highlight .gi {
  color: #b8bb26;
  background-color: #282828;
  }
  .highlight .gd {
  color: #fb4934;
  background-color: #282828;
  }
  .highlight .gh {
  color: #b8bb26;
  font-weight: bold;
  }
  .highlight .ge {
  font-style: italic;
  }
  .highlight .ges {
  font-weight: bold;
  font-style: italic;
  }
  .highlight .gs {
  font-weight: bold;
  }
  .highlight .k, .highlight .kn, .highlight .kp, .highlight .kr, .highlight .kv {
  color: #fb4934;
  }
  .highlight .kc {
  color: #d3869b;
  }
  .highlight .kt {
  color: #fabd2f;
  }
  .highlight .kd {
  color: #fe8019;
  }
  .highlight .s, .highlight .sb, .highlight .sc, .highlight .dl, .highlight .sd, .highlight .s2, .highlight .sh, .highlight .sx, .highlight .s1 {
  color: #b8bb26;
  font-style: italic;
  }
  .highlight .si {
  color: #b8bb26;
  font-style: italic;
  }
  .highlight .sr {
  color: #b8bb26;
  font-style: italic;
  }
  .highlight .sa {
  color: #fb4934;
  }
  .highlight .se {
  color: #fe8019;
  }
  .highlight .nn {
  color: #8ec07c;
  }
  .highlight .nc {
  color: #8ec07c;
  }
  .highlight .no {
  color: #d3869b;
  }
  .highlight .na {
  color: #b8bb26;
  }
  .highlight .m, .highlight .mb, .highlight .mf, .highlight .mh, .highlight .mi, .highlight .il, .highlight .mo, .highlight .mx {
  color: #d3869b;
  }
  .highlight .ss {
  color: #83a598;
  }
}
